import { Component, OnInit, Input } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
  FormArray
} from "@angular/forms";
import { EmployeeService } from "../services/employee-service";

@Component({
  selector: "app-add-employee",
  templateUrl: "./add-employee.component.html",
  styleUrls: ["./add-employee.component.css"]
})
export class AddEmployeeComponent implements OnInit {
  @Input() employeeId: string;
  @Input() name: string;
  @Input() salary: number;
  public inputsForm: FormGroup;
  employeeName: string;
  baseSalary: number = 0;
  takeHome: number = 0;
  constructor(
    private formBuilder: FormBuilder,
    private employeeService: EmployeeService
  ) {}

  ngOnInit() {
    this.inputsForm = this.formBuilder.group({
      employeeName: "",
      baseSalary: 0,
      deductions: this.formBuilder.array([this.createItem()])
    });
    this.employeeName = this.name;
    this.baseSalary = this.salary;
    if (this.employeeId !== undefined) {
      this.employeeService.getEmployee(this.employeeId).subscribe(res => {
        this.inputsForm.value.deductions = res;
      });
    }
  }
  calculateTakeHome(formValue) {
    console.log(formValue.deductions);
    let deductionsArray = formValue.deductions.map(d => d.value);
    let totalDeductions = deductionsArray.reduce((acc, curr) => acc + curr);

    this.takeHome = this.baseSalary - totalDeductions;
  }
  createItem(): FormGroup {
    return this.formBuilder.group({
      name: "",
      value: 0
    });
  }
  get deductions(): FormArray {
    return this.inputsForm.get("deductions") as FormArray;
  }
  addItem(): void {
    this.deductions.push(this.createItem());
  }
  public OnSubmit(formValue: any) {
    //if used for adding employee
    if (this.employeeId === undefined) {
      this.employeeService
        .addEmployee(formValue)
        .subscribe(res => console.log(res));
      alert("Employee Saved");
      console.log(formValue);
    } else {
      //for updating employee
      this.employeeService
        .editEmployee(formValue, { emp_id: this.employeeId })
        .subscribe(res => {
          alert("successful!");
          return console.log(res);
        });
    }
  }
}



